@use 'variables' as *;

/* CSS for overarching elements and root structure */
* {
	margin: 0;
	padding: 0;
	font-family: Arial, Helvetica, sans-serif;
	box-sizing: border-box;
	color: $softBlack;
}
body {
	font-size: 16px;
	overflow-x: hidden;
}
h1 {
	font-size: 3rem;
	margin-bottom: 1rem;
}
h2 {
	font-size: 2.5rem;
	margin-bottom: 1rem;
}
h3 {
	font-size: 2rem;
	margin-bottom: 1rem;
}
p {
	font-size: 1.2rem;
	margin-bottom: 1rem;
}
main {
	background-color: $white;
	padding-top: 1.5rem;
}
.list {
	li {
		margin-bottom: 1rem;
	}
}
h4 {
	margin-bottom: 1rem;
}
h5 {
	margin-bottom: 1rem;
}
.heading {
	color: $darkerBlue;
	text-align: center;
	width: 100%;
	display: block;
}
.marginBot_15rem {
	margin-bottom: 1.5rem;
}
.marginTop_15rem {
	margin-top: 1.5rem;
}
.padBot_100 {
	padding-bottom: 100px;
}
.blueText {
	color: $darkerBlue !important;
}
.whiteText {
	color: $white;
}
.white-bg {
	background-color: $white;
}
.grey-bg {
	background-color: grey;
}
.anta {
	font-family: "Anta", sans-serif;
	font-style: normal;
}
.centerText {
	text-align: center;
}
.leftText {
	text-align: left !important;
}
.width70 {
	width: 70%;
	margin: 0 auto;
}
.width90 {
	width: 90%;
	margin: 0 auto;
}
.test {
	color: $white !important;
	position: absolute;
	width: 100%;
	text-align: center;
	top: 4%;
}
